.container-NavBar{
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid lightgrey;
    background-color: aliceblue;
}

h1{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
    margin: 15px 10px;
    padding-left: 10px;
    text-align: start;
}

.titulo-NavBar{
    width: 50%;
    height: 70px;
}


.menu{
    width: 50%;
    height: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.menu-NavBar{
    width: 15%;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

.menu-NavBar:hover{
    color: #303F9F;
    border-bottom: medium solid blue;
}