.container-habilidades{
    width: 100%;
    height: 610px;
    display: flex;
    flex-direction: column;
}

h2{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
    margin: 30px;
}

.titulo{
    width: 100%;
    height: 88px;
    text-align: center;
    justify-content: center;
}

.div-class-hab{
    height: 504px;
    width: 100%;
}

.hab-1{
    grid-area: habilidade1;
}

.hab-1 :hover{
    background-color: lightgray;
}

.class-hab{
    height: 504px;
    width: 70%;
    margin: 0 auto;
    display: grid;
    gap: 2%;
    grid-template-areas: 
        "habilidade1 . .";
    grid-template-columns: 250px 250px 250px;
    grid-template-rows: 200px;
    justify-content: center;
}

.class-hab > div{
    height: 200px;
    width: 250px;
    text-align: center;
    font-size: 20px;
    border: thin solid lightgrey;
}