.container-grid{
    width: 100%;
    height: 500px;
    display: grid;
    grid-template-areas: "profile resumo";
    grid-template-columns: 45% 45%;
    grid-template-rows: 500px;
    align-items: center;
    justify-content: center;
}

h2{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
}

.titulo{
    width: 100%;
    height: 100px;
    text-align: center;
    justify-content: center;
}

.profile-grid{
    grid-area: profile;
    height: 500px;
    margin-right: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.photo{
    width: 228px;
    height: 276px;
    align-items: center;
}

.resumo{
    grid-area: resumo;
    height: 500px;
    margin-top: 45px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.p{
    margin: 10px;
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

#textoresumo{
    height: 200px;
    line-height: 1.6;
}

button{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    padding: 12px;
    border-color: lightgray;
    background-color: white;
}

button:hover{
    background-color: aliceblue;
}

button:focus{
    box-shadow: 0 0 0 0;
    outline: 0;
}