.container{
    width: 100%;
    height: 663px;
    display: flex;
    flex-direction: column;
}

h2{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
    margin: 30px;
}

li{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 16px;
    padding: 10px 5px;
    margin: 10px 5px;
    list-style-type: none;
    float:left;
    clear:left;
}

.titulo{
    width: 100%;
    height: 88px;
    text-align: center;
    justify-content: center;
}

.filtro-portfolio{
    width: 100%;
    height: 12%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.div-class-Projetos{
    height: 504px;
    width: 100%;
}

.projeto-1{
    grid-area: projeto1;
}

.class-projetos{
    height: 450px;
    width: 70%;
    margin: 0 auto;
    display: grid;
    gap: 3%;
    grid-template-areas: 
        "projeto1 . ."
        ". . . ";
    grid-template-columns: 250px 250px 250px;
    grid-template-rows: 200px 200px;
    justify-content: center;
}

.class-projetos > div{
    height: 200px;
    width: 250px;
    text-align: center;
    font-size: 30px;
}