.container-contato{
    height: 150px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contatos{
    height: 80px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.imagem-contatos img{
    width: 40px;
    height: 40px;
    margin: 8px;
    padding: 7px;
}

footer{
    width: 280px;
    height: 70px;
    border-top: 1px solid lightgray;
}

.imagem-contatos span{
    display:none;
}

.imagem-contatos:hover #face{
    display:block;
}

.imagem-contatos:hover #facebw{
    display:none;
}

.imagem-contatos:hover #git{
    display:block;
}

.imagem-contatos:hover #gitbw{
    display:none;
}

.imagem-contatos:hover #kag{
    display:block;
}

.imagem-contatos:hover #kagbw{
    display:none;
}

.imagem-contatos:hover #link{
    display:block;
}

.imagem-contatos:hover #linkbw{
    display:none;
}