.container-servicos{
    width: 100%;
    height: 480px;
    display: flex;
    flex-direction: column;
}

h2{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
    margin: 30px;
}

.titulo{
    width: 100%;
    height: 88px;
    text-align: center;
    justify-content: center;
}

.div-class-servico{
    height: auto;
    width: 100%;
}

.class-servico{
    height: auto;
    width: 53%;
    display: grid;
    margin: 0 auto;
    gap: 10%;
    grid-template-areas: 
        "servico1 servico2 ";
    grid-template-columns: 320px 320px;
    grid-template-rows: 380px;
    
}

.servico-1{
    grid-area: servico1;
    height: 380px;
    width: 320px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: thin solid lightgray;
}

.servico-2{
    grid-area: servico2;
    border-width: medium;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0);
    height: 380px;
    width: 320px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: thin solid lightgray;
}

.img-servico{
    width: 250px;
    height: 250px;
    margin: 10px;
}

.texto{
    width: 320px;
    height: 30px;
    font-size: 20px;
    text-align: center;
    justify-content: center;
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.imagem span{
    display:none;
}

.servico-1:hover span{
    display:block;
}
.servico-1:hover #imgbw{
    display:none;
}

.servico-2:hover span{
    display:block;
}
.servico-2:hover #imgbw{
    display:none;
}